<template>
  <About class="section" :weight="weight" />
  <Skills class="section" />
  <Toast class="section" />
  <MyStream class="section" />
  <Exes class="section" />
  <Sweeter class="section" />
</template>

<script>
import About from '@/components/About.vue';
import Skills from '@/components/Skills.vue';
import Toast from '@/components/Toast.vue';
import MyStream from '@/components/MyStream.vue';
import Exes from '@/components/Exes.vue';
import Sweeter from '@/components/Sweeter.vue';
import fullpage from 'fullpage.js';
import 'fullpage.js/dist/fullpage.min.css';
import 'fullpage.js/dist/fullpage.extensions.min.js';
import ScrollReveal from 'scrollreveal';

export default {
  components: {
    About,
    Skills,
    Toast,
    MyStream,
    Exes,
    Sweeter
  },

  data: () => ({
    weight: 0,
    scrolled: 0,
    moveTo: 0,
    ids: ["about", "skills", "toast", "mystream", "exes", "sweeter"]
  }),

  mounted() {
    this.updateColor();

    ScrollReveal().reveal("h1", { scale: 0,});
    ScrollReveal().reveal("h2", { scale: 0 });
    ScrollReveal().reveal("#khu-logo, #dimigo-logo, #soma-logo", { opacity: 0 } );
    ScrollReveal().reveal("#skills .skill-icon", { scale: 0 });
    ScrollReveal().reveal("#about > img, #toast > div > img, #mystream > div > img, #exes > div > img, #sweeter > div > img", { opacity: 0 });
    ScrollReveal().reveal("p, .text", { scale: 0 });
    ScrollReveal().reveal(".icon-link", { scale: 0 });

    /*new fullpage("#app", {
      dragAndMove: true,
    });*/
    window.addEventListener("scroll", this.onScroll);
    window.addEventListener("resize", this.updateColor);
    //window.addEventListener("wheel", this.onWheel, { passive: false });
  },

  unmounted() {
    window.removeEventListener("scroll", this.onScroll);
    window.removeEventListener("resize", this.updateColor);
    //window.removeEventListener("wheel", this.onWheel, { passive: false });
  },

  methods: {
    updateColor() {
      var backgrounds = [
        [141, 32, 30],
        [255, 255, 255],
        [255, 122, 47],
        [28, 127, 254],
        [3, 21, 42],
        [194, 23, 91]
      ];

      var texts = [
        [255, 255, 255],
        [0, 0, 0],
        [255, 255, 255],
        [255, 255, 255],
        [255, 255, 255],
        [255, 255, 255]
      ];

      this.weight = window.scrollY / window.innerHeight;
      var index = Math.floor(this.weight);
      if(this.weight - index <= 0.1) {
        this.weight = index;
      }
      else if(this.weight - index >= 0.9) {
        this.weight = index + 1;
      }
      else {
        this.weight = index + (this.weight - index - 0.1) / 0.8;
      }

      var weight = this.weight - index;

      var bgLast = backgrounds[backgrounds.length - 1];
      var textLast = texts[texts.length - 1];
      if(index == backgrounds.length - 1) {
        document.body.style.backgroundColor = `rgb(${bgLast[0]}, ${bgLast[1]}, ${bgLast[2]})`;
        document.body.style.color = `rgb(${textLast[0]}, ${textLast[1]}, ${textLast[2]})`;
        return;
      }

      var bgR = Math.floor(backgrounds[index][0] * (1 - weight) + backgrounds[index + 1][0] * weight);
      var bgG = Math.floor(backgrounds[index][1] * (1 - weight) + backgrounds[index + 1][1] * weight);
      var bgB = Math.floor(backgrounds[index][2] * (1 - weight) + backgrounds[index + 1][2] * weight);
      var textR = Math.floor(texts[index][0] * (1 - weight) + texts[index + 1][0] * weight);
      var textG = Math.floor(texts[index][1] * (1 - weight) + texts[index + 1][1] * weight);
      var textB = Math.floor(texts[index][2] * (1 - weight) + texts[index + 1][2] * weight);

      document.body.style.backgroundColor = `rgb(${bgR}, ${bgG}, ${bgB})`;
      document.body.style.color = `rgb(${textR}, ${textG}, ${textB})`;
    },

    onScroll(e) {
      if(window.scrollY >= this.moveTo)
        this.moveTo = 0;

      for(var i = this.scrolled + 1; i < this.ids.length; i++) {
        if(window.scrollY >= document.getElementById(this.ids[i]).offsetTop)
          this.scrolled = i;
      }

      this.updateColor();
    },

    onWheel(e) {
      console.log("scrolY: " + window.scrollY);
      console.log("scrollTop: " + document.documentElement.scrollTop);
      console.log("moveTo: " + this.moveTo);
      if(window.scrollY < this.moveTo) {
        e.preventDefault();
        return;
      }

      if(e.deltaY <= 0 || this.scrolled == this.ids.length - 1)
        return;
        
      e.preventDefault();
      this.handleScrollDown();
    },

    onKeydown(e) {
      if(e.keyCode != 38 && e.keyCode != 40 && e.keyCode != 33 && e.keyCode != 34)
        return;
      
      if(window.scrollY < this.moveTo) {
        e.preventDefault();
        return;
      }

      if(e.keyCode == 38 || e.keyCode == 33 || this.scrolled == this.ids.length - 1)
        return;
      
      e.preventDefault();
      this.handleScrollDown();
    },

    handleScrollDown() {
      var current = document.getElementById(this.ids[this.scrolled]);
      var next = document.getElementById(this.ids[this.scrolled + 1]);

      if(window.scrollY >= current.offsetTop) {
        console.log("next");
        var isSafari = /constructor/i.test(window.HTMLElement) || (function (p) { return p.toString() === "[object SafariRemoteNotification]"; })(!window['safari'] || (typeof safari !== 'undefined' && window['safari'].pushNotification));
        //document.documentElement.scrollTop = next.offsetTop;
        window.scrollTo({top: next.offsetTop + (isSafari ? 1 : 0), behavior: "smooth"});
        this.scrolled++;
        this.moveTo = next.offsetTop;
      }
      else {
        window.scrollBy({top: 200, behavior: "smooth"});
      }
    }
  }
}
</script>

<style lang="scss">
  .full-content {
    height: 100vh;
    min-width: 900px;
    min-height: 768px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h1 {
      margin: 0;
      font-size: 72px;
    }
  }
</style>