<template>
  <section id="about" class="full-content">
    <h1>About me</h1>
    <img id="picture" width="264" height="264" src="@/assets/picture.jpg" />
    <div class="text">이름: 마수현(Ma Suhyeon)</div>
    <div class="text">생년월일: 1997년 7월 12일</div>
    
    <h2>학력 및 활동</h2>
    <div id="khu-logo" :class="{ hidden: hideLogos }"><img src="@/assets/khu.svg" /></div>
    <div id="khu-text" class="text">경희대학교 컴퓨터공학과 졸업</div>
    <div id="dimigo-logo" :class="{ hidden: hideLogos }"><img src="@/assets/dimigo.png" /></div>
    <div id="dimigo-text" class="text">한국디지털미디어고등학교 웹프로그래밍과 졸업</div>
    <div id="soma-logo" :class="{ hidden: hideLogos }"><img src="@/assets/soma.png" /></div>
    <div id="soma-text" class="text">제9기 SW마에스트로 인증</div>

    <canvas id="about-canvas"></canvas>
  </section>
</template>

<script>
export default {

  data: () => ({
    hideLogos: false
  }),

  props: ["weight"],

  mounted() {
    this.drawCanvas();
    window.addEventListener("resize", this.drawCanvas);
  },

  unmounted() {
    window.removeEventListener("resize", this.drawCanvas);
  },

  methods: {
    drawCanvas() {
      var section = document.getElementById("about");
      var rect = section.getBoundingClientRect();

      var canvas = document.getElementById("about-canvas");
      canvas.width = rect.width;
      canvas.height = rect.height;

      var ctx = canvas.getContext("2d");
      ctx.clearRect(0, 0, rect.width, rect.height);

      this.hideLogos = window.innerWidth < 1160;
      if(this.hideLogos)
        return;

      ctx.lineWidth = 4;
      var weight = Math.min(Math.max(this.weight, 0), 1);
      var r = 126 * (1 - weight) + 255 * (weight);
      var g = 2 * (1 - weight) + 255 * (weight);
      var b = 0 * (1 - weight) + 255 * (weight);
      ctx.fillStyle = `rgb(${r}, ${g}, ${b})`;
      ctx.strokeStyle = ctx.fillStyle;

      var khuLogo = document.getElementById("khu-logo");
      var khuTextRect = document.getElementById("khu-text").getBoundingClientRect();
      khuTextRect.x -= rect.x;
      khuTextRect.y -= rect.y;
      var fromX = khuTextRect.x - 12;
      var fromY = khuTextRect.y + khuTextRect.height / 2;
      this.drawLogoAndLine(ctx, khuLogo, fromX, fromY, Math.max(fromX - 300, 128), fromY - 300);

      var dimigoLogo = document.getElementById("dimigo-logo");
      var dimigoTextRect = document.getElementById("dimigo-text").getBoundingClientRect();
      dimigoTextRect.x -= rect.x;
      dimigoTextRect.y -= rect.y;
      fromX = dimigoTextRect.right + 12;
      fromY = dimigoTextRect.y + dimigoTextRect.height / 2;
      this.drawLogoAndLine(ctx, dimigoLogo, fromX, fromY, Math.min(fromX + 300, rect.width - 128), rect.height / 2);

      var somaLogo = document.getElementById("soma-logo");
      var somaTextRect = document.getElementById("soma-text").getBoundingClientRect();
      somaTextRect.x -= rect.x;
      somaTextRect.y -= rect.y;
      this.drawLogoAndLine(ctx, somaLogo, somaTextRect.x - 12, somaTextRect.y + somaTextRect.height / 2, 200, somaTextRect.y + somaTextRect.height / 2);
    },

    drawLogoAndLine(ctx, el, fromX, fromY, toX, toY) {
      var rect = el.getBoundingClientRect();
      el.style.top = `${toY - rect.height / 2}px`;
      el.style.left = `${toX - rect.width / 2}px`;

      ctx.beginPath();
      ctx.arc(fromX, fromY, 6, 0, Math.PI * 2);
      ctx.closePath();
      ctx.fill();

      ctx.beginPath();
      ctx.moveTo(fromX, fromY);
      ctx.lineTo((fromX + toX) / 2, fromY);
      ctx.lineTo(toX, toY);
      ctx.stroke();
    }
  },

  watch: {
    weight() {
      this.drawCanvas();
    }
  }

}
</script>

<style lang="scss" scoped>
  #picture {
    position: relative;

    width: 264px;
    height: 264px;
    border-radius: 132px;
  }

  canvas {
    position: absolute;
    top: 0;
    left: 0;

    z-index: -1;
  }

  @keyframes hover {
    from {
      transform: translateY(-2px);
    }

    50% {
      transform: translateY(2px);
    }

    to {
      transform: translateY(-2px);
    }
  }

  #picture {
    display: block;
    margin: 24px auto;
  }

  h2 {
    font-size: 20px;
    margin: 12px 0 0 0;
  }

  #khu-logo, #dimigo-logo, #soma-logo {
    position: absolute;
    display: flex;
    width: 192px;
    height: 192px;
    box-sizing: border-box;
    border-radius: 96px;
    align-items: center;
    padding: 18px;

    background-color: white;
    box-shadow: 6px 6px 25px rgba(0, 0, 0, 0.16);

    animation-duration: 1s;
    animation-name: hover;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;

    &.hidden {
      display: none;
    }

    img {
      width: 100%;
    }
  }
</style>